"use client"

import { X } from "lucide-react"
import QuoteForm from "./quote-form"
import Script from "next/script"

interface QuoteModalProps {
  open: boolean
  onClose: () => void
  source?: string
}

export default function QuoteModal({ open, onClose, source }: QuoteModalProps) {
  if (!open) return null

  return (
    <>
      <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50" onClick={onClose}>
        <div 
          id="container"
          className="bg-white shadow-xl mx-4 max-h-[90vh] overflow-y-auto"
          style={{
            textAlign: 'left',
            background: '#fff',
            border: '1px solid #ccc',
            boxShadow: 'rgba(0,0,0,0.2) 0px 0px 5px',
            margin: '0 auto',
            width: '640px',
            maxWidth: '100%',
            padding: '0'
          }}
          onClick={(e) => e.stopPropagation()}
        >
          {/* Logo */}
          <div id="logo" style={{
            margin: 0,
            padding: 0,
            minHeight: 0,
            backgroundColor: '#dedede',
            textIndent: '-9000px',
            textDecoration: 'none'
          }}>
            <a href="#" style={{
              minHeight: '40px',
              height: '40px',
              display: 'block',
              background: 'url(/images/wflogo.png) no-repeat left top',
              overflow: 'hidden'
            }}>Wufoo</a>
          </div>

          {/* Close Button */}
          <button
            onClick={onClose}
            className="absolute top-2 right-2 p-2 hover:bg-gray-100 rounded-full transition z-10"
            style={{ position: 'absolute', top: '10px', right: '10px' }}
          >
            <X className="w-5 h-5 text-gray-600" />
          </button>

          {/* Form */}
          <div style={{ margin: '20px 14px 0 14px', padding: '0 0 20px 0', position: 'relative' }}>
            {/* Info Section */}
            <div className="info" style={{
              display: 'inline-block',
              clear: 'both',
              margin: '0 0 5px 0',
              padding: '0 1% 1.1em 1%',
              borderBottom: '1px dotted #ccc'
            }}>
              <h2 style={{
                fontWeight: 'normal',
                fontSize: '160%',
                margin: '0 0 5px 0',
                clear: 'left'
              }}>Contact Us</h2>
            </div>

            <QuoteForm onSuccess={onClose} source={source} />

            {/* Footer */}
            <div style={{ textAlign: 'center', padding: '20px 0', color: '#666', borderTop: '1px dotted #ccc', marginTop: '20px' }}>
              <div style={{ marginBottom: '10px' }}>
                <img src="/images/powerlogo.png" alt="Wufoo" style={{ border: 'none' }} />
              </div>
              <div style={{ fontSize: '85%' }}>Designed</div>
            </div>
          </div>
        </div>
      </div>
      <Script src="/scripts/wufoo.js" strategy="lazyOnload" />
    </>
  )
}
